___CSS_LOADER_EXPORT___.push([module.id, `* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    user-select: none;
}

body {
    font-family: 'Roboto', sans-serif;
    color: black;
    overflow: hidden;
    scroll-behavior: smooth;
}

#background {
    position: absolute;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    z-index: -1;
}

header {
    margin: auto;
    font-size: 2.5vw;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    border-bottom: 1vh solid rgb(255, 255, 255);
}

#headerCanvas {
    background-color: black;
    position: fixed;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    z-index: -1;
}

main {
    position: relative;
    height: 100vh;
    width: 100vw;
}

#displayTitle {
    position: absolute;
    top: 3%;
    left: 1%;
    width: 20vw;
    height: 5vh;
    min-width: fit-content;
    min-height: fit-content;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: rgba(25, 25, 25, 0.50);
    backdrop-filter: blur(10px);
    color: white;
    font-size: 2vh;
    padding: 1vh 10vw;
}

#mainCanvas {
    background-color: rgb(0, 0, 0);
    position: relative;
    z-index: -1;
}
